import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {StorageManagerProvider} from "../../providers/storage-manager/storage-manager";
import {TouchManagerProvider} from "../../providers/touch-manager/touch-manager";
import {GuiManagerProvider} from "../../providers/gui-manager/gui-manager";

/**
 * Generated class for the UserSettingsFingerprintActivationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-settings-fingerprint-activation',
  templateUrl: 'user-settings-fingerprint-activation.html',
})
export class UserSettingsFingerprintActivationPage {

  activatedBiometricLogin: boolean = true;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: StorageManagerProvider, private touchManager: TouchManagerProvider, private guiManager: GuiManagerProvider) {
    this.loadStatus();
  }

  ionViewDidLoad() {
    this.loadStatus();
  }

  loadStatus() {
    this.storage.contains("touch-ignore", (does) => {
      if (does) {
        this.activatedBiometricLogin = false;
      }
      else {
        this.storage.contains("touch-info", (contains) => {
          this.activatedBiometricLogin = !!contains;
        })
      }
    });
  }

  disableBiometric() {
    const me = this;
    this.touchManager.disableTouch(() => {
      me.loadStatus();
    });
  }

  enableBiometric() {
    const me = this;
    this.touchManager.activateTouch(() => {
      me.guiManager.showPopupMessage("Biometrisches Login", "Wenn Sie sich das nächste Mal in die W-App einloggen, haben Sie die Möglichkeit, Ihr Passwort mit einem biometrischen Login (Fingerprint oder Face-ID) zu verbinden, sofern dies von Ihrem Gerät unterstützt wird.");
      me.loadStatus();
      me.navCtrl.pop().then( navigationSuccess => {
        console.log("Navigation successful!", navigationSuccess)
      }).catch(navigCatch => {
        console.log("Navigation failed...", navigCatch)
      })
    });
  }
}



